// this makes each middle card higher
@media (min-width: 1000px) {
  .container-tours {
    div.container-tour-cards {
      height: 450px;
    }
  }
  .container-accomodation-cards {
    height: 450px;
  }
  .container-shop {
    height: 450px;
  }
}

